@import "helpers/config";

.icon{
  height: 24px;
  width: 24px;
  stroke-width: 2px;
  stroke: $dark;
  @include transition(200ms);
}
#search .icon{ stroke: $dark; }

header, #menu, .img-card div{
  background-color: $extra;
}


// transition effects only where needed
nav .nav__sections, 
#menu, 
.img-card div, 
section[class^="social-"]{
  @include transition(200ms);
}


// login/register buttons
.acct{
  display: block;
  @include buttonStyle;

  font-size: 1rem;
  padding: $pad * 1.5;
  flex-grow: 1;
  margin: calc($pad / 2.25);
}


.dark_theme{
  background-color: $dark !important;
  nav{
    background-color: $dark !important;
    color: $main !important;

    .icon{ stroke: $main !important; }
    @include media($tablet){
      #search .icon{ stroke: $dark !important; }
    }
  }
  footer{ 
    color: $main !important; 
    .icon{ stroke: $main; }
  }
}