// color palette
$main: magenta;
$light: #E1E1F2;
$dark: #2f2f2f;
$extra: #7073A5;

// fonts
$fontText: Garamond, serif;
$fontTitle: Audiowide, "Lucida Handwriting", cursive;

// spacings
$pad: 7px;
$marg: 25px;

// medias
$small: "max-width: 300px";
$phone: "min-width: 301px";
$bigPhone: "min-width: 500px";
$tablet: "min-width: 768px";
$laptop: "min-width: 992px";
$desktop: "min-width: 1200px";
$huge: "min-width: 1800px";

// for label in account
$spBreak1: "min-width: 427px";


// border styling
$radius: 8px;
@function bord($w: 1px, $s: ridge, $c: $main){
  @return $w $s $c; 
}
