@import "./helpers/imports";

#product__wrapper{
  @include flex;
  justify-content: space-around;
}
// button to quickly go to checkout
.forCheck{
  width: 100%;
}
.goToCheckout{
  @include buttonStyle;
  position: absolute;
  left: 1.5%;
  width: auto;

  @include media($bigPhone){
    left: 3px;
  }
  writing-mode: vertical-lr;
  @include transform(rotate(180deg));

  a{
    display: block;
    padding: $pad * 1.5;
  }
}

// item's card --------------------------
#cart-items{
  padding: $marg 0;
  @include flex;
  justify-content: space-around;
  align-content: space-around;
}
.item{
  color: $dark;
  background-color: $extra;
  border: bord(2px, $c: $dark);
  width: 97%;
  margin: $marg 0;
  @include media($bigPhone){
    margin: $marg;
  }
  overflow: hidden;
  border-radius: $radius;

  @include media($bigPhone){
    width: 350px;
  }
  
  .labels{
    font: {
      size: 20px;
      weight: bold;
    }
  }
  .price{
    font: {
      size: 22px;
      weight: bold;
    }
  }
  .image img{
    width: 100%;
    height: auto;
    @include media($bigPhone){
      height: 300px;
    }
  }
  .quantity{
    @include flex;
    
    .quanDesign{
      @include flex(nowrap);
      justify-content: flex-end;

      .quantity__modifier{
        display: inline-block;
        padding: 10px 15px;
        background-color: $extra;
        border: bord(1px, solid, $dark);
        height: 40px;
        font-weight: bold;
      }
      .quantity__modifier:not(input.quantity__modifier){
        cursor: pointer;
      }
      .quantity__modifier:first-child{
        border-radius: $radius 0 0 $radius;
      }
      input.quantity__modifier{
        width: 55px;
        border-width: 1px 0;
      }
      .quantity__modifier:last-child{
        border-radius: 0 $radius $radius 0;
      }
    }
  }
  
  article:not(.image){
    @include perfect_center;
  }
  
  article{
    padding: 10px 0;
  }
  /*--- REMOVE BUTTON ---*/
  #remove{
    @include transition(200ms);
    font: {
      weight: 900;
      size: 30px;
    }
    cursor: pointer;
    
    &:hover{
      background: $dark;
      color: $main;
    }
  }
  article[class]{
    @include flex;
    justify-content: space-between;
    border-bottom: bord(2px, $c: $dark);
    padding: 10px;
  
    div:not(.labels, .quanDesign div){
      text-align: right;
      flex-grow: 1;
    }
  }
}
#checkout{
  border: bord(2px, $c: $dark);
  width: 99%;
  background-color: $extra;
  * { color: $dark; }
  margin: 0 auto;
  border-radius: $radius;
  
  @mixin titles(){
    text-align: center; 
    margin: 0;
    
    font: {
      family: $fontTitle;
      size: 13vw;
    } 
    color: $dark;
    @include media($bigPhone){
      font-size: 60px;
    }
  }
  h1{
    @include titles;
    border-bottom: bord(2px, dashed, $dark);
    padding: 30px 0;
  } 
  #contain-checkout-info{
    padding: 2*$pad;
  }
  article{
    &:not(&:last-child){
      margin-bottom: 3*$pad;
    }
    @include flex;
    justify-content: space-between;

    .labels, .price{
      font: {
        size: 24px;
        weight: bold;
      }
      @include media($tablet){
        font-size: 30px;
      }
    }
    .labels{ 
      color: $dark; 
      @include media($small){
        font-size: 16px;
      }

      span{ color: crimson; }
    }
    .price{ 
      color: $dark; 
    }
  }

  #submitOrder{
    all: unset;
    display: block;
    width: 100%;
    @include titles;

    @include transition(200ms);
    border-top: bord(2px, $c: $dark); 
    padding: 15px 0;
    cursor: pointer;

    &:hover{
      color: $main;
      background-color: $dark;
    }
  }
  .right{
    text-align: right;
    @include flex($w: nowrap);
    justify-content: flex-end;
    width: 100%;

    input, select{
      display: block;
      width: 100%;
      padding: $pad;
      border-radius: $radius;
      color: $dark;
      background-color: $main;
      font-size: 20px;
      border: bord($c: $dark);

      @include media($tablet){
        font: { 
          size: 30px; 
          weight: bold;
        }
      }
      @include media($small){
        font-size: 16px;
      }
      &:focus{
        box-shadow: 0 1px 10px $main, 0 -1px 10px $main, 1px 0 10px $main, -1px 0 10px $main;
      }
    }
  }
  ::placeholder{
    color: $dark;
    font-weight: normal;
  }
  .coupon input{ border-radius: $radius 0 0 $radius; }
  #enterCoupon{
    all: unset;
    @include transition(200ms);
    border: bord($c: $dark);
    border-left: none;
    background-color: $dark;
    display: block;
    padding: 0 $pad;
    color: $main;

    height: 34px;
    font-weight: bold;
    
    @include media($phone){
      height: 36px;
      font-size: 20px;
    }
    @include media($tablet){
      height: 47px;
      font-size: 24px
    }
    border-radius: 0 $radius $radius 0;
    cursor: pointer;

    &:hover{
      background: $main;
      color: $dark;
    }
  }
  #error, #error span{
    text-align: left;
    color: crimson;
    font: {
      size: 30px;
      weight: bold;
    }
  }
}

// item's card end--------------------------