@mixin transform($v) {
  transform: $v;
  -o-transform: $v;
  -webkit-transform: $v;
  -moz-transform: $v;
  -ms-transform: $v;
}
@mixin filter($v) {
  filter: $v;
  -webkit-filter: $v;
}
@mixin transition($v) {
  transition: $v;
  -webkit-transition: $v;
  -o-transition: $v;
  -moz-transition: $v;
}
@mixin animation($n, $d, $it: infinite, $t: linear) {
  animation: $n $d $it $t;
  -webkit-animation: $n $d $it $t;
  -o-animation: $n $d $it $t;
  -moz-animation: $n $d $it $t;
}
@mixin linear_gradient($v) {
  background: linear-gradient($v);
  background: -ms-linear-gradient($v);
  background: -moz-linear-gradient($v);
  background: -o-linear-gradient($v);
  background: -webkit-linear-gradient($v);
}
@mixin radial_gradient($v) {
  background: radial-gradient($v);
  background: -ms-radial-gradient($v);
  background: -moz-radial-gradient($v);
  background: -o-radial-gradient($v);
  background: -webkit-radial-gradient($v);
}
@mixin conic_gradient($v) {
  background: conic-gradient($v);
  background: -ms-conic-gradient($v);
  background: -moz-conic-gradient($v);
  background: -o-conic-gradient($v);
  background: -webkit-conic-gradient($v);
}
@mixin flex($dir: row, $w: wrap) {
  display: flex;
  display: -webkit-flex;
  flex-flow: $dir $w;
}
@mixin grid {
  display: grid;
  display: -ms-grid;
  display: -moz-grid;
}
@mixin box_size($v: border-box) {
  box-sizing: $v;
  -webkit-box-sizing: $v;
  -moz-box-sizing: $v;
}
@mixin transform_origin($v) {
  transform-origin: $v;
  -moz-transform-origin: $v;
  -webkit-transform-origin: $v;
  -ms-transform-origin: $v;
  -o-transform-origin: $v;
}
@mixin perfect_center{
  @include flex;
  justify-content: center;
  align-items: center;
}
@mixin media($breakpoint) {
  @media only screen and ($breakpoint) {
    @content;
  }
}
@mixin buttonStyle{
  border: bord(1px, $c: $main);
  border-radius: $radius;
  @include transition(200ms);
  height: 45px;

  background-color: $dark;
  color: $main;
  cursor: pointer;

  font: {
    family: $fontTitle;
    weight: 600;
  }
  text-align: center;

  &:hover{
    background-color: $main;
    color: $dark;
    border: bord($c: $dark);
  }
}
@mixin dimensions($w: auto, $h: auto){
  width: $w;
  height: $h;
}