@import './helpers/imports';


/*---------------- FOOTER ----------------*/
footer{
  color: $dark;
  text-align: center;
  font: {
    weight: bold;
    size: 22px;
  }
  border-top: bord(2px, $c: $extra);
  
  position: relative;
  z-index: 2;
  padding: $pad*1.5;
  margin-top: $marg;

  box-shadow: 
    0 -1px 20px 5px $main,
    0 -1px 20px 5px $main;

  // responsive footer 
  @include media($small){
    font-size: 16px;
  }
  @include media($bigPhone){
    @include grid;
    grid-template-columns: auto auto;
    text-align: left;
    justify-content: space-between;
  }

  // responsve icon
  .icon{
    // stroke: $dark;
    @include media($small){
      height: 16px;
      width: 16px;
    }
    @include media($tablet){
      height: 30px;
      width: 30px;
    }
  }
  
  #copyright {
    order: 1;
  }
  .social-whatsapp {
    order: 2;
  }
  #locate {
    order: 3;
  }
  .social-instagram {
    order: 4;
  } 
  @include media($tablet){
    font-size: 30px;
  }
  section a {
    width: 100%;
    @include transition(200ms);

    &:hover{ text-decoration: underline; }
  }
}
footer .icon{
  position: relative;
   top: 5px;
}
/*---------------- FOOTER END ----------------*/