@import "./helpers/imports";

label, .h8t{
  @include transition(400ms);
}

// highlight error message
.error{
  font: {
    family: $fontText;
    size: 24px;
    weight: 900
  }
  color: rgb(180, 3, 39);
  padding: $pad;
}


// animation box
#anime{
  background-color: transparent;
  @include dimensions(98%, 500px);
  font-family: $fontTitle;
  border-radius: $radius;
  
  position: relative;
  padding: 2px;
  margin: $marg auto;

  // hide out rotation
  overflow: hidden;

  // responsiveness
  @include media($bigPhone){
    max-width: 500px;
  }
  @include media($laptop){
    max-width: 600px;
  }

  #infos{
    background: $dark;
    @include dimensions(100%, 100%);
    border-radius: $radius;

    position: relative;
    z-index: 1;

    h1{
      margin: 0;
      padding: 40px 0;
      color: $dark;
      text-align: center;
    }
    .form1{
      section[id]{
        padding-bottom: 40px;
        position: relative;
      }
      .inputs{
        all: unset;
        color: $dark;
        background-color: transparent;
        width: 95%;

        padding: 10px;
        text-align: left;
        
        position: relative;
        @include media($bigPhone){
          left: 2px;
        }
        @include media($laptop){
          left: 5.095px;
        }

        z-index: 2;
        font: {
          family: $fontText;
          weight: bold; 
        }
      }
      // these are labels
      .describe{
        color: $extra;
        padding: 10px;

        position: absolute;
        z-index: 1;
        left: 2.5%;
        @include transform(translateY(-41px));

        @include media($spBreak1){
          @include transform(none);
        }
      }
      // background animation
      .h8t{
        background-color: $main;
        @include dimensions(98%, 1px);
        border-radius: $radius;

        position: absolute;
        left: 1%;
      }
      
      // transitions
      input:focus ~ label, 
      input:valid ~ label{
        @include transform(translate(-15px, -66px));
        font-size: 13px;

        @include media($spBreak1){
          @include transform(translate(-15px, -28px));
        }
      }
      input:focus ~ label ~ .h8t,
      input:valid ~ label ~ .h8t{
        height: 38px;
        @include transform(translateY(-38px));
      }
      // buttons -------------------
      #buttons{
        @include flex($dir: column);
        width: 100%;

        @include media($bigPhone){
          @include flex;
          justify-content: space-around;
        }
      }
    }
  }
}
.register{
  height: auto !important;
}
// add a before background
#anime::before{
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 100%;
  height: 500px;

  // set the rotation origin (center)
  transform-origin: bottom right;

  // colors
  background: linear-gradient(to bottom, $main, $main, transparent);

  // rotate anime
  @include animation(colRotation1, 7s);
  @keyframes colRotation1 {
    from{ @include transform(rotate(0deg)); }
    to{ @include transform(rotate(360deg)); }
  }
}
// add an after background
#anime::after{
  content: "";
  position: absolute;
  
  // position it relatively until the side touch the center of the main container
  top: 50%;
  left: 50%;
  width: 100%;
  height: 500px;

  // set the rotation origin (center)
  transform-origin: top left;

  // colors
  background: linear-gradient(to top, $main, $main, transparent);

  // rotate anime
  @include animation(colRotation2, 7s);
  @keyframes colRotation2 {
    from{ @include transform(rotate(0deg)); }
    to{ @include transform(rotate(360deg)); }
  }
}