@import "./helpers/imports";
 
/*---------------- sidebar MENU ----------------*/
.modal{
  // visibility: hidden;
  background-color: rgba(0,0,0,0.5);
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9;
  width: 100%;
  height: 100%;
}
#menu{
  text-align: center;
  font-family: $fontTitle;
  position: fixed;
  width: 0;
  height: 100%;
  overflow: hidden;
  z-index: 10;
  top:0;
  right: 0;
  @include transition(200ms);

  // ---------- design and styles ------------
  // remove icon
  span{
    position: absolute;
    font-size: 40px;
    right: 2.5%;
    cursor: pointer;
    @include transition(200ms);

    &:hover{ color: $main; }
  }

  // title
  h2{
    color: $dark;
    font-size: 28px; 
    text-align: center;
    border-bottom: 2px dashed $dark;
    margin: 0;
    padding: 40px 0 15px;
    @include media($bigPhone){
      padding-top: 20px;
    }
  }

  // links
  a{
    @include transition(200ms);
    display: block;
    padding: $pad $pad*2;
    font-size: 22px;

    &:not(a:last-child){
      border-bottom: 1px dashed $dark;
    }
    &:hover{
      color: $main;
      background-color: $dark;
    }
  }
}
::placeholder{
  color: $dark;
}
/*---------------- sidebar MENU END ----------------*/