* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

::selection {
  background-color: magenta;
  color: #2f2f2f;
}

input::selection {
  background-color: #2f2f2f;
  color: magenta;
}

body {
  overflow-x: hidden;
  background-color: #E1E1F2;
  margin: 0;
  font-family: Garamond, serif;
}

a {
  text-decoration: none;
  color: unset;
}

.icon {
  height: 24px;
  width: 24px;
  stroke-width: 2px;
  stroke: #2f2f2f;
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
}

#search .icon {
  stroke: #2f2f2f;
}

header, #menu, .img-card div {
  background-color: #7073A5;
}

nav .nav__sections,
#menu,
.img-card div,
section[class^=social-] {
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
}

.acct {
  display: block;
  border: 1px ridge magenta;
  border-radius: 8px;
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
  height: 45px;
  background-color: #2f2f2f;
  color: magenta;
  cursor: pointer;
  font-family: Audiowide, "Lucida Handwriting", cursive;
  font-weight: 600;
  text-align: center;
  font-size: 1rem;
  padding: 10.5px;
  flex-grow: 1;
  margin: 3.1111111111px;
}
.acct:hover {
  background-color: magenta;
  color: #2f2f2f;
  border: 1px ridge #2f2f2f;
}

.dark_theme {
  background-color: #2f2f2f !important;
}
.dark_theme nav {
  background-color: #2f2f2f !important;
  color: magenta !important;
}
.dark_theme nav .icon {
  stroke: magenta !important;
}
@media only screen and (min-width: 768px) {
  .dark_theme nav #search .icon {
    stroke: #2f2f2f !important;
  }
}
.dark_theme footer {
  color: magenta !important;
}
.dark_theme footer .icon {
  stroke: magenta;
}