label, .h8t {
  transition: 400ms;
  -webkit-transition: 400ms;
  -o-transition: 400ms;
  -moz-transition: 400ms;
}

.error {
  font-family: Garamond, serif;
  font-size: 24px;
  font-weight: 900;
  color: rgb(180, 3, 39);
  padding: 7px;
}

#anime {
  background-color: transparent;
  width: 98%;
  height: 500px;
  font-family: Audiowide, "Lucida Handwriting", cursive;
  border-radius: 8px;
  position: relative;
  padding: 2px;
  margin: 25px auto;
  overflow: hidden;
}
@media only screen and (min-width: 500px) {
  #anime {
    max-width: 500px;
  }
}
@media only screen and (min-width: 992px) {
  #anime {
    max-width: 600px;
  }
}
#anime #infos {
  background: #2f2f2f;
  width: 100%;
  height: 100%;
  border-radius: 8px;
  position: relative;
  z-index: 1;
}
#anime #infos h1 {
  margin: 0;
  padding: 40px 0;
  color: #2f2f2f;
  text-align: center;
}
#anime #infos .form1 section[id] {
  padding-bottom: 40px;
  position: relative;
}
#anime #infos .form1 .inputs {
  all: unset;
  color: #2f2f2f;
  background-color: transparent;
  width: 95%;
  padding: 10px;
  text-align: left;
  position: relative;
  z-index: 2;
  font-family: Garamond, serif;
  font-weight: bold;
}
@media only screen and (min-width: 500px) {
  #anime #infos .form1 .inputs {
    left: 2px;
  }
}
@media only screen and (min-width: 992px) {
  #anime #infos .form1 .inputs {
    left: 5.095px;
  }
}
#anime #infos .form1 .describe {
  color: #7073A5;
  padding: 10px;
  position: absolute;
  z-index: 1;
  left: 2.5%;
  transform: translateY(-41px);
  -o-transform: translateY(-41px);
  -webkit-transform: translateY(-41px);
  -moz-transform: translateY(-41px);
  -ms-transform: translateY(-41px);
}
@media only screen and (min-width: 427px) {
  #anime #infos .form1 .describe {
    transform: none;
    -o-transform: none;
    -webkit-transform: none;
    -moz-transform: none;
    -ms-transform: none;
  }
}
#anime #infos .form1 .h8t {
  background-color: magenta;
  width: 98%;
  height: 1px;
  border-radius: 8px;
  position: absolute;
  left: 1%;
}
#anime #infos .form1 input:focus ~ label,
#anime #infos .form1 input:valid ~ label {
  transform: translate(-15px, -66px);
  -o-transform: translate(-15px, -66px);
  -webkit-transform: translate(-15px, -66px);
  -moz-transform: translate(-15px, -66px);
  -ms-transform: translate(-15px, -66px);
  font-size: 13px;
}
@media only screen and (min-width: 427px) {
  #anime #infos .form1 input:focus ~ label,
  #anime #infos .form1 input:valid ~ label {
    transform: translate(-15px, -28px);
    -o-transform: translate(-15px, -28px);
    -webkit-transform: translate(-15px, -28px);
    -moz-transform: translate(-15px, -28px);
    -ms-transform: translate(-15px, -28px);
  }
}
#anime #infos .form1 input:focus ~ label ~ .h8t,
#anime #infos .form1 input:valid ~ label ~ .h8t {
  height: 38px;
  transform: translateY(-38px);
  -o-transform: translateY(-38px);
  -webkit-transform: translateY(-38px);
  -moz-transform: translateY(-38px);
  -ms-transform: translateY(-38px);
}
#anime #infos .form1 #buttons {
  display: flex;
  display: -webkit-flex;
  flex-flow: column wrap;
  width: 100%;
}
@media only screen and (min-width: 500px) {
  #anime #infos .form1 #buttons {
    display: flex;
    display: -webkit-flex;
    flex-flow: row wrap;
    justify-content: space-around;
  }
}

.register {
  height: auto !important;
}

#anime::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 100%;
  height: 500px;
  transform-origin: bottom right;
  background: linear-gradient(to bottom, magenta, magenta, transparent);
  animation: colRotation1 7s infinite linear;
  -webkit-animation: colRotation1 7s infinite linear;
  -o-animation: colRotation1 7s infinite linear;
  -moz-animation: colRotation1 7s infinite linear;
}
@keyframes colRotation1 {
  from {
    transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
  }
}

#anime::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 500px;
  transform-origin: top left;
  background: linear-gradient(to top, magenta, magenta, transparent);
  animation: colRotation2 7s infinite linear;
  -webkit-animation: colRotation2 7s infinite linear;
  -o-animation: colRotation2 7s infinite linear;
  -moz-animation: colRotation2 7s infinite linear;
}
@keyframes colRotation2 {
  from {
    transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
  }
}