/*---------------- sidebar MENU ----------------*/
.modal {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9;
  width: 100%;
  height: 100%;
}

#menu {
  text-align: center;
  font-family: Audiowide, "Lucida Handwriting", cursive;
  position: fixed;
  width: 0;
  height: 100%;
  overflow: hidden;
  z-index: 10;
  top: 0;
  right: 0;
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
}
#menu span {
  position: absolute;
  font-size: 40px;
  right: 2.5%;
  cursor: pointer;
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
}
#menu span:hover {
  color: magenta;
}
#menu h2 {
  color: #2f2f2f;
  font-size: 28px;
  text-align: center;
  border-bottom: 2px dashed #2f2f2f;
  margin: 0;
  padding: 40px 0 15px;
}
@media only screen and (min-width: 500px) {
  #menu h2 {
    padding-top: 20px;
  }
}
#menu a {
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
  display: block;
  padding: 7px 14px;
  font-size: 22px;
}
#menu a:not(a:last-child) {
  border-bottom: 1px dashed #2f2f2f;
}
#menu a:hover {
  color: magenta;
  background-color: #2f2f2f;
}

::placeholder {
  color: #2f2f2f;
}

/*---------------- sidebar MENU END ----------------*/