/*------------ luffy & bros background ------------*/
#bg-image {
  background-image: url(../../public/images/luffy.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  height: calc(150vw - 27px);
}
@media only screen and (min-width: 685px) {
  #bg-image {
    background-size: cover;
  }
}
@media only screen and (min-width: 768px) {
  #bg-image {
    background-image: url(../../public/images/3-bros.jpg);
    background-size: contain;
    height: calc(50vw - 8px);
  }
}
#bg-image section[class] {
  height: 100%;
  position: relative;
  z-index: 2;
  text-align: center;
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  color: magenta;
  background: rgba(0, 0, 0, 0.6);
  font-family: Audiowide, "Lucida Handwriting", cursive;
  font-size: 14vw;
}
@media only screen and (min-width: 768px) {
  #bg-image section[class] {
    font-size: 10vw;
  }
}

/*------------ luffy & bros background end ------------*/
/*---------------- CATALOG -----------------*/
#main-content {
  text-align: center;
}
#main-content h1 {
  color: #2f2f2f;
  padding: 46.9px 0;
  margin: 0;
  font-family: Audiowide, "Lucida Handwriting", cursive;
  font-size: 55px;
}
@media only screen and (max-width: 300px) {
  #main-content h1 {
    font-size: 40px;
  }
}
@media only screen and (min-width: 500px) {
  #main-content h1 {
    font-size: 70px;
  }
}

#img-contain {
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
}

.img-card {
  margin-bottom: 25px;
  width: 90vw;
}
@media only screen and (min-width: 500px) {
  .img-card {
    width: 75vw;
  }
}
@media only screen and (min-width: 768px) {
  .img-card {
    width: 300px;
    margin: 25px;
  }
}
.img-card div {
  border: 2px ridge #2f2f2f;
  border-radius: 8px;
  overflow: hidden;
}
.img-card div img {
  background-color: #2f2f2f;
  width: 100%;
  height: auto;
  border-bottom: 2px ridge #2f2f2f;
}
@media only screen and (min-width: 768px) {
  .img-card div img {
    height: 250px;
  }
}
.img-card div article {
  padding: 7px;
  color: #2f2f2f;
  font-size: 20px;
  font-weight: bold;
}
.img-card div:hover {
  border-color: #2f2f2f;
  cursor: pointer;
  box-shadow: 0 4px 30px 20px magenta, 0 6px 40px 20px magenta;
}

/*---------------- CATALOG END -----------------*/