/*------------------- NAVIGATION BAR -------------------*/
nav,
#left,
#homeTab,
#right {
  display: flex;
  display: -webkit-flex;
  flex-flow: nowrap wrap;
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}

nav {
  position: sticky;
  top: 0;
  justify-content: space-between;
  z-index: 4;
  color: #2f2f2f;
  background-color: #E1E1F2;
  border-bottom: 1px ridge magenta;
  box-shadow: 0 1px 3px magenta;
}
nav #left,
nav #right {
  height: 50px;
}
nav .nav__sections {
  display: block;
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 14px;
  align-items: center;
}
nav .nav__sections:hover:not(#search) {
  cursor: pointer;
  color: #2f2f2f;
  background-color: magenta;
}
nav .nav__sections:hover:not(#search) .icon {
  stroke: #2f2f2f !important;
}
nav #homeTab {
  font-size: 20px;
  font-weight: bold;
}
nav #homeTab aside {
  display: none;
}
@media only screen and (min-width: 301px) {
  nav #homeTab aside {
    display: block;
  }
}
nav #right {
  justify-content: right;
}
@media only screen and (min-width: 768px) {
  nav #right {
    width: 80%;
  }
}
nav #search {
  flex-grow: 3;
  padding: 0 14px;
  position: relative;
}
nav #search input {
  all: unset;
  display: none;
  background-color: magenta;
  color: #2f2f2f;
  padding-left: 30px;
  width: 100%;
  height: 80%;
  border: 2px ridge magenta;
  border-radius: 8px;
  cursor: text;
  font-size: 20px;
  font-weight: 900;
}
@media only screen and (min-width: 768px) {
  nav #search input {
    display: block;
  }
}
nav #search input:focus {
  border-color: #2f2f2f;
}
nav #search .icon {
  z-index: 4;
  left: 18px;
}
@media only screen and (min-width: 768px) {
  nav #search .icon {
    position: absolute;
    color: #2f2f2f;
  }
}
@media only screen and (min-width: 768px) {
  nav #search {
    width: 82%;
  }
}

/*------------------- NAVIGATION BAR -------------------*/