/*---------------- FOOTER ----------------*/
footer {
  color: #2f2f2f;
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  border-top: 2px ridge #7073A5;
  position: relative;
  z-index: 2;
  padding: 10.5px;
  margin-top: 25px;
  box-shadow: 0 -1px 20px 5px magenta, 0 -1px 20px 5px magenta;
}
@media only screen and (max-width: 300px) {
  footer {
    font-size: 16px;
  }
}
@media only screen and (min-width: 500px) {
  footer {
    display: grid;
    display: -ms-grid;
    display: -moz-grid;
    grid-template-columns: auto auto;
    text-align: left;
    justify-content: space-between;
  }
}
@media only screen and (max-width: 300px) {
  footer .icon {
    height: 16px;
    width: 16px;
  }
}
@media only screen and (min-width: 768px) {
  footer .icon {
    height: 30px;
    width: 30px;
  }
}
footer #copyright {
  order: 1;
}
footer .social-whatsapp {
  order: 2;
}
footer #locate {
  order: 3;
}
footer .social-instagram {
  order: 4;
}
@media only screen and (min-width: 768px) {
  footer {
    font-size: 30px;
  }
}
footer section a {
  width: 100%;
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
}
footer section a:hover {
  text-decoration: underline;
}

footer .icon {
  position: relative;
  top: 5px;
}

/*---------------- FOOTER END ----------------*/