@import "./helpers/imports";


/*------------ luffy & bros background ------------*/
#bg-image{
  background-image: url(../../public/images/luffy.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  height: calc(150vw - 27px);
  
  @include media("min-width: 685px"){
    background-size: cover;
  }
  
  @include media($tablet){
    background-image: url(../../public/images/3-bros.jpg);
    background-size: contain;
    height: calc(50vw - 8px);
  }
  section[class]{
    height: 100%;
    position: relative;
    z-index: 2;
    text-align: center;
    @include perfect_center;

    color: $main;
    background: rgba(0,0,0,0.6);
    font: {
      family: $fontTitle;
      size: 14vw;
    } 
    @include media($tablet){
      font-size: 10vw;
    }
  }
}
/*------------ luffy & bros background end ------------*/


/*---------------- CATALOG -----------------*/
#main-content{
  text-align: center;

  // title
  h1{
    color: $dark;
    padding: 46.9px 0;
    margin: 0;
    font: {
      family: $fontTitle;
      size: 55px;
    }
    @include media($small){
      font-size: 40px;
    }
    @include media($bigPhone){
      font-size: 70px;
    }
  }
}
#img-contain{
  @include flex;
  justify-content: space-around;
  align-items: center;
}
// image
.img-card{
  margin-bottom: $marg;
  width: 90vw;
  @include media($bigPhone){
    width: 75vw;
  }
  @include media($tablet){
    width: 300px;
    margin: $marg;
  }

  div{
    border: bord(2px, $c: $dark);
    border-radius: $radius;
    overflow: hidden;

    img{
      background-color: $dark;
      @include dimensions(100%);
      @include media($tablet){
        height: 250px;
      }
      border-bottom: bord(2px, $c: $dark)
    }
    article{
      padding: $pad;
      color: $dark;
      font: {
        size: 20px; 
        weight: bold;
      }
    }
    &:hover{
      border-color: $dark;
      cursor: pointer;
      box-shadow:
        0 4px 30px 20px $main,
        0 6px 40px 20px $main;
    }
  }
}
/*---------------- CATALOG END -----------------*/
