#product__wrapper {
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.forCheck {
  width: 100%;
}

.goToCheckout {
  border: 1px ridge magenta;
  border-radius: 8px;
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
  height: 45px;
  background-color: #2f2f2f;
  color: magenta;
  cursor: pointer;
  font-family: Audiowide, "Lucida Handwriting", cursive;
  font-weight: 600;
  text-align: center;
  position: absolute;
  left: 1.5%;
  width: auto;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  -o-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
}
.goToCheckout:hover {
  background-color: magenta;
  color: #2f2f2f;
  border: 1px ridge #2f2f2f;
}
@media only screen and (min-width: 500px) {
  .goToCheckout {
    left: 3px;
  }
}
.goToCheckout a {
  display: block;
  padding: 10.5px;
}

#cart-items {
  padding: 25px 0;
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: space-around;
}

.item {
  color: #2f2f2f;
  background-color: #7073A5;
  border: 2px ridge #2f2f2f;
  width: 97%;
  margin: 25px 0;
  overflow: hidden;
  border-radius: 8px;
  /*--- REMOVE BUTTON ---*/
}
@media only screen and (min-width: 500px) {
  .item {
    margin: 25px;
  }
}
@media only screen and (min-width: 500px) {
  .item {
    width: 350px;
  }
}
.item .labels {
  font-size: 20px;
  font-weight: bold;
}
.item .price {
  font-size: 22px;
  font-weight: bold;
}
.item .image img {
  width: 100%;
  height: auto;
}
@media only screen and (min-width: 500px) {
  .item .image img {
    height: 300px;
  }
}
.item .quantity {
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
}
.item .quantity .quanDesign {
  display: flex;
  display: -webkit-flex;
  flex-flow: nowrap wrap;
  justify-content: flex-end;
}
.item .quantity .quanDesign .quantity__modifier {
  display: inline-block;
  padding: 10px 15px;
  background-color: #7073A5;
  border: 1px solid #2f2f2f;
  height: 40px;
  font-weight: bold;
}
.item .quantity .quanDesign .quantity__modifier:not(input.quantity__modifier) {
  cursor: pointer;
}
.item .quantity .quanDesign .quantity__modifier:first-child {
  border-radius: 8px 0 0 8px;
}
.item .quantity .quanDesign input.quantity__modifier {
  width: 55px;
  border-width: 1px 0;
}
.item .quantity .quanDesign .quantity__modifier:last-child {
  border-radius: 0 8px 8px 0;
}
.item article:not(.image) {
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}
.item article {
  padding: 10px 0;
}
.item #remove {
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
  font-weight: 900;
  font-size: 30px;
  cursor: pointer;
}
.item #remove:hover {
  background: #2f2f2f;
  color: magenta;
}
.item article[class] {
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
  justify-content: space-between;
  border-bottom: 2px ridge #2f2f2f;
  padding: 10px;
}
.item article[class] div:not(.labels, .quanDesign div) {
  text-align: right;
  flex-grow: 1;
}

#checkout {
  border: 2px ridge #2f2f2f;
  width: 99%;
  background-color: #7073A5;
  margin: 0 auto;
  border-radius: 8px;
}
#checkout * {
  color: #2f2f2f;
}
#checkout h1 {
  text-align: center;
  margin: 0;
  font-family: Audiowide, "Lucida Handwriting", cursive;
  font-size: 13vw;
  color: #2f2f2f;
  border-bottom: 2px dashed #2f2f2f;
  padding: 30px 0;
}
@media only screen and (min-width: 500px) {
  #checkout h1 {
    font-size: 60px;
  }
}
#checkout #contain-checkout-info {
  padding: 14px;
}
#checkout article {
  display: flex;
  display: -webkit-flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
#checkout article:not(#checkout article:last-child) {
  margin-bottom: 21px;
}
#checkout article .labels, #checkout article .price {
  font-size: 24px;
  font-weight: bold;
}
@media only screen and (min-width: 768px) {
  #checkout article .labels, #checkout article .price {
    font-size: 30px;
  }
}
#checkout article .labels {
  color: #2f2f2f;
}
@media only screen and (max-width: 300px) {
  #checkout article .labels {
    font-size: 16px;
  }
}
#checkout article .labels span {
  color: crimson;
}
#checkout article .price {
  color: #2f2f2f;
}
#checkout #submitOrder {
  all: unset;
  display: block;
  width: 100%;
  text-align: center;
  margin: 0;
  font-family: Audiowide, "Lucida Handwriting", cursive;
  font-size: 13vw;
  color: #2f2f2f;
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
  border-top: 2px ridge #2f2f2f;
  padding: 15px 0;
  cursor: pointer;
}
@media only screen and (min-width: 500px) {
  #checkout #submitOrder {
    font-size: 60px;
  }
}
#checkout #submitOrder:hover {
  color: magenta;
  background-color: #2f2f2f;
}
#checkout .right {
  text-align: right;
  display: flex;
  display: -webkit-flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  width: 100%;
}
#checkout .right input, #checkout .right select {
  display: block;
  width: 100%;
  padding: 7px;
  border-radius: 8px;
  color: #2f2f2f;
  background-color: magenta;
  font-size: 20px;
  border: 1px ridge #2f2f2f;
}
@media only screen and (min-width: 768px) {
  #checkout .right input, #checkout .right select {
    font-size: 30px;
    font-weight: bold;
  }
}
@media only screen and (max-width: 300px) {
  #checkout .right input, #checkout .right select {
    font-size: 16px;
  }
}
#checkout .right input:focus, #checkout .right select:focus {
  box-shadow: 0 1px 10px magenta, 0 -1px 10px magenta, 1px 0 10px magenta, -1px 0 10px magenta;
}
#checkout ::placeholder {
  color: #2f2f2f;
  font-weight: normal;
}
#checkout .coupon input {
  border-radius: 8px 0 0 8px;
}
#checkout #enterCoupon {
  all: unset;
  transition: 200ms;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  -moz-transition: 200ms;
  border: 1px ridge #2f2f2f;
  border-left: none;
  background-color: #2f2f2f;
  display: block;
  padding: 0 7px;
  color: magenta;
  height: 34px;
  font-weight: bold;
  border-radius: 0 8px 8px 0;
  cursor: pointer;
}
@media only screen and (min-width: 301px) {
  #checkout #enterCoupon {
    height: 36px;
    font-size: 20px;
  }
}
@media only screen and (min-width: 768px) {
  #checkout #enterCoupon {
    height: 47px;
    font-size: 24px;
  }
}
#checkout #enterCoupon:hover {
  background: magenta;
  color: #2f2f2f;
}
#checkout #error, #checkout #error span {
  text-align: left;
  color: crimson;
  font-size: 30px;
  font-weight: bold;
}