@import "./helpers/imports";

/*------------------- NAVIGATION BAR -------------------*/
nav,
#left,
#homeTab,
#right {
  @include flex(nowrap);
  @include perfect_center;
}
nav {
  position: sticky;
  top: 0;
  justify-content: space-between;
  z-index: 4;
  color: $dark;
  background-color: $light;
  border-bottom: bord();
  box-shadow: 0 1px 3px $main;

  #left,
  #right {
    height: 50px;
  }
  .nav__sections {
    display: block;
    @include perfect_center;
    height: 100%;
    padding: $pad * 2;
    align-items: center;

    &:hover:not(#search) {
      .icon{ stroke: $dark !important; }
      cursor: pointer;
      color: $dark;
      background-color: $main;
    }
  }
  #homeTab {
    font: {
      size: 20px;
      weight: bold;
    }
    aside {
      display: none;
      @include media($phone) {
        display: block;
      }
    }
  }
  #right {
    justify-content: right;
    @include media($tablet) {
      width: 80%;
    }
  }
  // input field SEARCH BAR ------------------
  #search {
    flex-grow: 3;
    padding: 0 $pad * 2;
    position: relative;

    input {
      all: unset;
      display: none;
      background-color: $main;
      color: $dark;
      padding-left: 30px;

      @include dimensions(100%, 80%);
      border: bord(2px);
      border-radius: $radius;
      cursor: text;

      font: {
        size: 20px;
        weight: 900;
      }
      @include media($tablet) {
        display: block;
      }
      &:focus {
        border-color: $dark;
      }
    }
    .icon {
      z-index: 4;
      left: 18px;
      @include media($tablet) {
        position: absolute;
        color: $dark;
      }
    }
    @include media($tablet) {
      width: 82%;
    }
  }
}
/*------------------- NAVIGATION BAR -------------------*/
