/*------------------- HEADER -------------------*/
header {
  padding: 2px;
  display: flex;
  display: -webkit-flex;
  flex-flow: column wrap;
  justify-content: center;
  border-bottom: 1px ridge magenta;
}
@media only screen and (min-width: 500px) {
  header {
    flex-direction: row;
    justify-content: space-between;
  }
}
header #logo img {
  display: block;
  margin: auto;
  width: 150px;
  height: auto;
}
header #account {
  display: flex;
  display: -webkit-flex;
  flex-flow: column wrap;
  width: 100%;
}
@media only screen and (min-width: 500px) {
  header #account {
    width: 50%;
    flex-direction: row;
    align-items: center;
  }
}

/*------------------- HEADER -------------------*/