@import 'helpers/imports';

/*------------------- HEADER -------------------*/
header{
  padding: 2px;
  @include flex($dir: column);
  justify-content: center;
  border-bottom: bord();

  @include media($bigPhone){
    flex-direction: row;
    justify-content: space-between;
  }

  #logo img{
    display: block;
    margin: auto;
    @include dimensions(150px);
  }

  #account{
    @include flex(column);
    width: 100%;
    
    @include media($bigPhone){
      width: 50%;
      flex-direction: row;
      align-items: center;
    }
  }
}
 
/*------------------- HEADER -------------------*/